.top-bar {
  display: flex;
  justify-content: space-between;
  padding-block: 16px;
  align-items: center;
  @include on-desktop {
  padding-block: 28px;
  z-index: 1;
  }

  &__nav {
    @include on-phone {
      display: none;
    }

    @include on-desktop {
      display: block;
    }
}
  &__list {
    display: flex;
    flex-direction: row;
    gap: 64px
  }
}
